import {Component, OnInit, TemplateRef, ViewChild} from '@angular/core';
import {CategoriseServices} from '../../../services/categories/categories.services';
import {Router} from '@angular/router';
import {gride} from './categorise.column';
import {GridDataResult} from '@progress/kendo-angular-grid';
import {State} from '@progress/kendo-data-query';
import {FilterToQueryService} from '../../../services/filter-to-query/filter-to-query.service';
import {NbDialogService, NbToastrService} from '@nebular/theme';
import {SharedApiServiceService} from '../../../services/shared/shared-api-service.service';
import {saveAs} from 'file-saver';

@Component({
  selector: 'app-category',
  templateUrl: 'categorise.component.html',
  styleUrls: ['categorise.component.scss'],
})
export class CategoriseComponent implements OnInit {
    @ViewChild('dialog') dialog: any;
    data = [];
    columnGrid = gride;
    dataItem: GridDataResult = {
        data: [],
        total: 0,
    };
    autoCompleteData: any = {
        name: 'category',
        list: []
    };
    loading: boolean = false;
    currentDeleteCategory: any;
    searchCategoriesId: any;
    lastMeta: string;
    public showgride: boolean = false;

    constructor(
        private categoryService: CategoriseServices,
        private router: Router,
        private convertFilter: FilterToQueryService,
        private dialogService: NbDialogService,
        private toaster: NbToastrService,
        private sharedService: SharedApiServiceService,
    ) {
    }

    ngOnInit() {
        this.getCategorise();
    }

    getCategorise(meta?: string) {
        this.lastMeta = meta;
        this.loading = true;
        this.categoryService.getCategorise(meta).subscribe(
            (res: any) => {
                this.dataItem.data = res['data'];
                this.dataItem.total = res.total;
            },
            error => {
                this.loading = false;
            },
            () => {
                this.loading = false;
            }
        );
    }

    getParentCategory(meta?: string) {
        this.categoryService.getParentCategorise(meta).subscribe(
            res => {
                this.autoCompleteData.list = res['data'];
            },
            error => {
            },
            () => {
            },
        );
    }

    filterChange(event: State) {
        if (event.filter){
            for (let i = 0; i < event.filter.filters.length; i++) {
                if (event.filter.filters[i]['field'] === 'id') {
                    this.searchCategoriesId = event.filter.filters[i]['value'];
                }
            }
        }
        this.getCategorise(this.convertFilter.convertToQuery(event));
    }

    autoCompleteFilterChangeCheck(event: any) {
        const meta = 'name=' + event.value;
        this.getParentCategory(meta);
    }

    deleteCategory(dialogRef: any) {
        this.categoryService.deleteCategory(this.currentDeleteCategory.id).subscribe(
            res => {
                this.toaster.show(res['msg'], 'Success', {status: 'success'});
            },
            error => {
                this.toaster.show(error.error.message, 'Error', {status: 'danger'});
                dialogRef.close();
            },
            () => {
                dialogRef.close();
                this.getCategorise(this.lastMeta);
                this.currentDeleteCategory = null;
            },
        );
    }

    commandActionClick(event: any) {
        switch (event.action.action) {
            case 'edit':
                this.router.navigate(['./categorise/edit-category/' + event.data.id]);
                break;

            case 'delete':
                this.currentDeleteCategory = event.data;
                this.open(this.dialog);
                break;

        }
    }

    open(dialog: TemplateRef<any>) {
        this.dialogService.open(dialog);
    }

    export() {
        this.sharedService.exportUrl = this.searchCategoriesId ? 'categories/export/excel?category_id=' + this.searchCategoriesId : 'categories/export/excel';
        this.sharedService.exportData(this.sharedService.exportUrl).subscribe(data => {
            saveAs(data, 'categories.xlsx');
        }),
            error =>  this.toaster.show('Error downloading the file.', 'Error', {status: 'danger'});
    }
}
